import React, {useEffect, useRef} from 'react'
import Container from 'react-bootstrap/Container'


export const SectionWrapper = ({ children, noPadding, className }) => {
  const elem = useRef(null)

    useEffect(() => {
        console.log(elem.current.querySelector('.wh-wrap'));
        if(elem.current) {
            // elem.current.querySelector('.wh-wrap').style.height = `${elem.current.querySelector('.horizontal-scroll__container').width}px`
        }

    }, [elem])

  return  (
      <Container
          ref={elem}
          className={`section-wrapper${noPadding ? ' no-padding' : ''}${className ? ` ${className}` : ''}`}
          children={children}
          onScroll={(e) => {
           elem.current.querySelector('.horizontal-scroll__container').style.transform = `translate3d(-${elem.current.scrollTop}px, 0, 0)`;
          }}
      />
  )
}
